.address {
  background-color: #fff;
  position: relative;
  margin-bottom: 20rpx;
  image {
    width: 100%;
  }
  .address-s {
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    padding: 0 20rpx;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 120rpx;
    .a-s-t {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .open-setting {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.5);
    text-align: center;
    display: flex;
    align-items: center;
    transform: translate(0, 0);
    button {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80% !important;
    }
  }
}

.goods-list {
  display: flex;
  background-color: #f8f8f8;
  image {
    width: 160rpx;
    height: 160rpx;
    margin-right: 8rpx;
  }
}

.money {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.submit {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20rpx 30rpx;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #fff;
  .cfff {
    padding: 20rpx 30rpx;
    border-radius: 60rpx;
    background-color: #ff5975;
    margin-left: 20rpx;
  }
}
page {
  width: 100%;
  height: 100%;
}
.pay-success {
  width: 100%;
  height: 100%;
  text-align: center;
  .bgfff {
    width: 100%;
    height: 100%;
  }
  .fw7 {
    font-size: 48rpx;
  }
  image {
    margin-top: 300rpx;
    display: inline-block;
    width: 446rpx;
    height: 370rpx;
  }
  button {
    background-color: #ff5975;
    border-radius: 88rpx;
    line-height: 88rpx;
    color: #fff;
    margin: 40rpx 30rpx 0 30rpx;
  }
}